/**
 * Polyfills
 *
 * @package Embark
 * @since   1.0.1
 */

import 'core-js/modules/es.array.iterator';
import 'core-js/modules/es.object.assign';
import 'picturefill';
import 'intersection-observer';
import 'url-search-params-polyfill';
import 'whatwg-fetch';
import stickybits from 'stickybits';
import objectFitImages from 'object-fit-images';
import objectFitVideos from 'object-fit-videos';

/**
 * Sticky elements (IE support) with Stickybits.
 * Stickybits is a lightweight alternative to position: sticky polyfills.
 * It works perfectly for things like sticky headers.
 *
 * @reference: https://github.com/dollarshaveclub/stickybits
 */
stickybits('.sticky', { scrollEl: 'body' });

/**
 * Initialize object-fit images and videos for unsupported browsers.
 */
objectFitImages();
objectFitVideos();

/**
 * Cross-browser requestAnimationFrame function.
 *
 * @since 1.0.1
//  */
window.requestAnimationFrame = window.requestAnimationFrame
|| window.mozRequestAnimationFrame
|| window.webkitRequestAnimationFrame
|| window.msRequestAnimationFrame
|| function (f) { setTimeout(f, 1000 / 60); };

/**
 * Element.closest() Polyfill
 *
 * @link https://developer.mozilla.org/en-US/docs/Web/API/Element/closest#Polyfill
 */
if (!Element.prototype.matches) {
	Element.prototype.matches = Element.prototype.msMatchesSelector || Element.prototype.webkitMatchesSelector;
}
if (!Element.prototype.closest) {
	Element.prototype.closest = function (s) {
		let el = this;
		do {
			if (el.matches(s)) return el;
			el = el.parentElement || el.parentNode;
		} while (el !== null && el.nodeType === 1);
		return null;
	};
}
